<template>
  <b-container>
    <article class="authpage">
      <v-card class="pa-2">
        <div class="text-center">
          <span class="mdi mdi-account-circle mdi-48px"></span>
          <br />
          <h3>تسجيل الدخول</h3>
          <v-form ref="signin" v-model="validity">
            <v-col cols="12" sm="12" class="inputstyle">
              <v-text-field
                v-model="email"
                label="الايميل"
                outlined
                :rules="emailRules"
                prepend-inner-icon="mdi-email"
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="12" class="inputstyle">
              <v-text-field
                v-model="password"
                label="كلمة السر"
                outlined
                :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="passwordRules"
                :type="show1 ? 'text' : 'password'"
                name="input-10-1"
                hint="At least 8 characters"
                counter
                @click:append="show1 = !show1"
                prepend-inner-icon="mdi-key"
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="12" class="inputstyle">
              <v-btn
                class=""
                color="primary"
                :loading="loading"
                outlined
                rounded
                @click="logins"
              >
                تسجيل الدخول
              </v-btn>
            </v-col>
          </v-form>
          <v-spacer></v-spacer>
          <p>
            <span>إن لم تكن مسجل انشئ حسابك .</span>
            <router-link to="/auth/register">انشئ حسابك</router-link>
          </p>
        </div>
      </v-card>
    </article>
  </b-container>
</template>
<script>
export default {
  data() {
    return {
      email: "",
      password: "",
      show: false,
      loading: false,

      //
      show1: false,
      show2: true,
      show3: false,
      show4: false,
      validity: false,
    };
  },
  computed: {
    emailRules() {
      return [
        (v) => !!v || "عدرا الإيميل فارغ",
        (v) => this.$pattern.email.test(v) || "ايميل غير صالح",
      ];
    },
    passwordRules() {
      return [
        (v) => !!v || "عدرا كلمة السر فارغ",
        (v) => this.$pattern.password.test(v) || "حدت خطأ في  كلمة السر",
      ];
    },
    valid() {
      return this.$refs.signin.validate();
    },
  },
  methods: {
    errorNotification(text, type = "error") {
      this.$swal.fire(type, text, "warning");
    },
    logins: function() {
      if (this.valid) {
        this.show = true;
        this.loading = true;
        if (this.email == "") {
          this.show = false;
          this.loading = false;
          this.errorNotification("عدرا الإيميل فارغ", "error");
        } else if (this.password == "") {
          this.show = false;
          this.loading = false;
          this.errorNotification("عدرا كلمة السر فارغ", "error");
        } else {
          this.$store
            .dispatch("signIn", {
              email: this.email,
              password: this.password,
            })
            .then((res) => {
              this.show = false;
              this.loading = false;
              if (res.status === 200 && res.data.code === 200) {
                if (this.$route.query.nextPath) {
                  this.$router
                    .push({
                      name: this.$route.query.nextPath,
                      params: this.$route.params,
                    })
                    .then(null, (err) => {
                      this.$router.push({ name: "NotFound" });
                      return err;
                    });
                } else this.$router.push({ name: "Home" });
              }
            })
            .catch(({ response }) => {
              this.loading = false;
              this.show = false;
              if (
                response?.status === 404 &&
                response?.data?.error?.message.code == 402
              ) {
                this.errorNotification("حدت خطأ في  كلمة السر ", "error");
              } else if (
                response?.status === 404 &&
                response?.data?.error?.message.code == 403
              ) {
                this.errorNotification("حدت خطأ أتناء تسجيل الدخول ", "error");
              } else if (
                response?.status === 404 &&
                response?.data?.error?.message.code == 404
              ) {
                this.errorNotification("كلمة السر أو الأيميل خاطئ ", "error");
              }
            });
        }
      }
    },
  },
};
</script>
